import React, { useState, useEffect } from 'react';
import "./SideBar.scss";
import { fileService, Note } from '../services/FileService';
import { Link, useParams, useLocation } from 'react-router-dom';




export const SideBar = () => {
  const [notes, setNotes] = useState([] as Note[]);
  const { category } = useParams();

  const notesPromise = fileService.getNotes(category!);

  const location = useLocation();

  useEffect(() => {
    notesPromise.then((s) => setNotes(s));
  }, [setNotes, notesPromise])

  return (
    <div className="side-bar">
      {
        notes.map((note) => {
          return (
            <div key={note.path} onClick={() => {
              if (note.hasPage) {
                document.location.href = note.path;
              }
            }} className={"item indent-" + note.level}>
              {note.hasPage
                ? (<Link to={note.path} className={note.path === location.pathname ? "active" : undefined}>{note.title}</Link>) :
                <span>{note.title}</span>}
            </div>)
        })
      }
    </div >
  )
}
