
export interface Note {
  path: string
  title: string
  level: number
  hasPage: boolean
}
export interface FileService {
  getNotes(category: string): Note[]
  getContent(path: string): string
  getBooks(): string[]
}

class FileServiceImpl {
  private categoryMap: Map<string, Note[]> = new Map();
  async getNotes(category: string): Promise<Note[]> {
    if (this.categoryMap.has(category)) {
      return this.categoryMap.get(category)!;
    }
    const url = "/md/" + category + "/" + category + ".toc";
    let response = await fetch(url);
    let text = await response.text();
    let lines = text.split("\n");
    let notes: Note[] = [];

    lines.forEach(line => {
      const fields = line.split(",", 2);
      if (fields.length !== 2) {
        return;
      }
      let path = fields[0];
      if (path[0] === '/') {
        path = path.slice(1);
      }
      let title = fields[1].trim();

      const segments = path.split("/");
      let baseName = segments[segments.length - 1];
      let level = segments.length - 1;
      if (baseName.endsWith("README.md")) {
        level -= 1;
      }

      if (level < 0) {
        // The root index, do not show in the side nav.
        return;
      }

      baseName = segments[level];
      let hasPage = true;
      if (title.length === 0) {
        hasPage = false;
        title = baseName.replace(".md", "");
      }
      notes.push({
        path: "/note/" + category + "/" + path,
        level: level,
        title: title,
        hasPage: hasPage,
      })
    });

    this.categoryMap.set(category, notes);
    return notes;
  }

  async getNoteContent(path: string): Promise<string> {
    if (path.startsWith("/")) {
      path = path.slice(1);
    }
    const url = "/md/" + path;
    let res = await fetch(url);
    return await res.text();
  }

  async getBooks(): Promise<string[]> {
    const url = "/md/root.toc";
    let res = await fetch(url);
    let text = await res.text();

    let books = text.split("\n")
    return books.map(x => x.trim()).filter(x => x.length > 0)
  }
}

export const fileService: FileServiceImpl = new FileServiceImpl();
