import React, { Fragment, ReactElement } from 'react';
// @ts-ignore
import * as emoji from 'emoji-dictionary';
import "./CodeBlock.scss";
import { isActiveTask, isDoneTask } from './TaskConstants';

interface Props {
  value: string
  data: Record<string, string>
}

const custom_map: Record<string, string> = {
  ':done:': ':heavy_check_mark:',
  ':active:': ':scroll:',
  ':compiled:': ':green_book:',
}

function getUnicode(name: string): string {
  if (name in custom_map) {
    name = custom_map[name];
  }
  let r = emoji.getUnicode(name);
  if (!r) {
    return emoji.getUnicode(":warning:");
  }
  return r;
}

function replaceEmoji(text: string): ReactElement {
  if (!text || text.length === 0) {
    return <Fragment>{text}</Fragment>
  }
  let elements: ReactElement[] = [];
  let matches = [...text.matchAll(/:\w+:/ig)];
  if (matches.length === 0) {
    return <Fragment>{text}</Fragment>;
  }
  let pos = 0;
  matches.forEach(m => {
    let start = m.index!;
    let s = m[0];
    let end = start + s.length;
    if (start > pos) {
      elements.push(<Fragment key={elements.length}>{text.substring(pos, start)}</Fragment>)
    }
    let taskClass = "";
    if (isActiveTask(s)) {
      taskClass += " active";
    }
    if (isDoneTask(s)) {
      taskClass += " done";
    }
    elements.push(<span key={elements.length} className={"emoji" + taskClass}>{getUnicode(s)}</span>)
    pos = end + 1;
  });
  if (pos < text.length) {
    elements.push(<Fragment key={elements.length + 1}>{text.substring(pos)}</Fragment>)
  }
  return <Fragment>{elements.map((e) => e)}</Fragment>;

}

function emojiSupport(textElement: ReactElement): ReactElement {
  if (typeof textElement.props.children === "string") {
    let text: string = textElement.props.children;
    return React.cloneElement(textElement, {}, replaceEmoji(text))
  }
  if (Array.isArray(textElement.props.children)) {
    let children: ReactElement[] = textElement.props.children!;
    return React.cloneElement(textElement, {},
      children.map(c => emojiSupport(c)));
  }

  return textElement;

  // return
}

function shortLinkSupport(text: string): ReactElement {
  let elements: ReactElement[] = [];
  if (!text || text.length === 0) {
    return <Fragment>{text}</Fragment>
  }
  let matches = [...text.matchAll(/(?<!\/)(b|go|t|cl|google3)\/[-a-zA-Z0-9]+/g)];

  if (matches.length === 0) {
    return <Fragment>{text}</Fragment>;
  }

  let pos = 0;
  matches.forEach(m => {
    let start = m.index!;
    let s = m[0];
    let end = start + s.length;
    if (start > pos) {
      elements.push(<Fragment key={elements.length}>{text.substring(pos, start)}</Fragment>)
    }
    elements.push(<a key={elements.length} href={"http://" + s}>{s}</a>)
    pos = end;
  });
  if (pos < text.length) {
    elements.push(<Fragment key={elements.length + 1}>{text.substring(pos)}</Fragment>)
  }
  return <Fragment>{elements.map((e) => e)}</Fragment>;
}

export const TextBlock = (props: Props) => {
  let ele = null;
  if (props.value.startsWith("__nolink__:")) {
    ele = <Fragment>{props.value.substr("__nolink__:".length)}</Fragment>
  } else {
    ele = shortLinkSupport(props.value);
  }
  return emojiSupport(ele);
}
