import React from 'react';
import "./CodeBlock.scss";
import { isActiveTask, isDoneTask } from "./TaskConstants"

interface Props {
  level: number
  children: any[]
}


export const Heading = (props: Props) => {

  let content = "";
  if (props.children && props.children.length > 0
    && props.children[0]!.props.value) {
    content = props.children[0]!.props.value;
  }
  const id = getIdFromProp(content);
  let headingClass = "";
  if (isActiveTask(content)) {
    headingClass += " active";
  }
  if (isDoneTask(content)) {
    headingClass += " done";
  }

  switch (props.level) {
    case 1:
      return <h1 id={id} className={headingClass}>{props.children}</h1>
    case 2:
      return <h2 id={id} className={headingClass}>{props.children}</h2>
    case 3:
      return <h3 id={id} className={headingClass}>{props.children}</h3>
    case 4:
      return <h4 id={id} className={headingClass}>{props.children}</h4>
    case 5:
      return <h5 id={id} className={headingClass}>{props.children}</h5>
  }
  return null;
}

export function getIdFromProp(value: string): string {
  if (!value) {
    return "";
  }
  return value.toLowerCase().replace(/[ /:{}]+/g, "-")
}
