import React, { useState } from 'react';
import "./CodeBlock.scss";

interface Props {
  language: string
  value: string
}

const contentCopyIcon = (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M16 1H4c-1.1 0-2 .9-2 2v14h2V3h12V1zm3 4H8c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h11c1.1 0 2-.9 2-2V7c0-1.1-.9-2-2-2zm0 16H8V7h11v14z" /></svg>
)

export const CodeBlock = (props: Props) => {
  const [showNotification, setShowNotification] = useState(false);
  function onClick() {
    if (navigator.clipboard) {
      navigator.clipboard.writeText(props.value).then(() => {
        displayTooltip();
      })
    }
  }

  function displayTooltip() {
    setShowNotification(true);

    setTimeout(() => {
      setShowNotification(false);
    }, 3000);
  }

  return (
    <div className="code-block">
      <div className={" tooltip" + (showNotification ? "" : " tooltip-hidden")} >Copied</div>
      <button className="codeblock-copy-btn"
        onClick={onClick}
      >{contentCopyIcon}</button>
      <pre className={" langauge-" + props.language}>
        <code className={" language-" + props.language} >{props.value}</code>
      </pre>
    </div>
  )
}
