import React, { useState } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect
} from "react-router-dom";
import './App.scss';
import { SideBar } from './components/SideBar';
import { ContentPage } from './components/ContentPage';
import { fileService } from './services/FileService';

const NavBar = () => {
  let [categories, setCategories] = useState(["loading"])

  if (categories[0] === "loading") {
    fileService.getBooks().then(
      cs => {
        if (cs[0] !== categories[0]) {
          setCategories(cs);
        }
      });
  }
  return (
    <nav className="nav-bar">
      <ul>
        {categories.map(c =>
          (<li >
            <Link key={c} to={`/note/${c}/README.md`}>{c[0].toUpperCase() + c.slice(1)}</Link>
          </li>)
        )}
      </ul>
    </nav >)
}

const ContentWithSideBar = () => {
  return (
    <div className="with-sidebar">
      <SideBar></SideBar>
      <ContentPage />
    </div>
  )
}

const App: React.FC = () => {
  return (
    <Router>
      <NavBar />
      <Switch>
        <Route path="/note/:category"><ContentWithSideBar /></Route>
      </Switch>
    </Router>
  );
}

export default App;
