import React, { createElement } from 'react';
import "./CodeBlock.scss";
import { isActiveTask, isDoneTask } from './TaskConstants';

interface Props {
  checked: boolean,
  children: any[],
}

function getCoreProps(props: any): Record<string, any> {
  return props['data-sourcepos'] ? { 'data-sourcepos': props['data-sourcepos'] } : {}
}

export const ListItem = (props: Props) => {
  let checkbox = null

  let classNames: Record<string, string> = {};
  if (props.children[0] && props.children[0].props.value) {
    const str: string = props.children[0].props.value;
    if (isActiveTask(str) || isDoneTask(str)) {
      classNames["className"] = "task";
    }
  } else if (props.checked !== null && props.checked !== undefined) {
    const checked = props.checked
    checkbox = createElement('input', { type: 'checkbox', checked, readOnly: true })
  }


  return <li {...classNames} {...getCoreProps(props)}>{checkbox} {props.children}</li>
}
