import { MarkdownAbstractSyntaxTree, RenderProps } from 'react-markdown'
import { getIdFromProp } from './Heading';

interface Heading {
  depth: number
  title: string
}

function postOrderVisit(root: MarkdownAbstractSyntaxTree, fn: (n: MarkdownAbstractSyntaxTree) => void) {
  if (root.children && root.children.length && root.children.length > 0) {
    root.children!.forEach((c) => postOrderVisit(c, fn))
  }
  fn(root);
}
function restoreEmptyLink(n: MarkdownAbstractSyntaxTree) {
  if (n.type !== 'linkReference') {
    return;
  }
  if (n.children && n.children.length === 1 && n.children[0].value) {
    n.children[0].value = "[" + n.children[0].value + "]"
  }

  return;
}

function fixDefinitionLink(n: MarkdownAbstractSyntaxTree) {
  if (n.type !== 'definition') {
    return;
  }
  if (n.url && n.url === '#') {
    // eslint-disable-next-line
    n.url = "javascript:void(0);";
  }
}

export function TocPlugin(node: MarkdownAbstractSyntaxTree, renderProps?: RenderProps): MarkdownAbstractSyntaxTree {
  const headings: MarkdownAbstractSyntaxTree[] = [];
  headings.push({
    data: { "class": "ABC" },
    type: "nav",
    ordered: false,
    depth: 0,
    children: [],
  });



  function unwrapChildrenLink(n: MarkdownAbstractSyntaxTree): MarkdownAbstractSyntaxTree[] {
    let content = "__nolink__:";
    if (n.children && n.children.length > 0) {
      n.children.forEach(c => {
        if (c.value) {
          content += c.value
        } else if (c.children && c.children.length > 0 && c.children[0].value) {
          content += c.children[0].value;
        }
      })
    }
    return [{
      type: "text",
      value: content
    }];
  }

  function collectHeading(n: MarkdownAbstractSyntaxTree) {
    if (n.type !== "heading" || !n.depth || n.depth <= 1) {
      return;
    }

    if (n.children && n.children.length === 0) {
      return;
    }
    let anchorId = getIdFromProp(n.children![0]!.value!);
    const item: MarkdownAbstractSyntaxTree =
    {
      type: "listItem",
      depth: n.depth,
      children: [{
        type: "link",
        url: "#" + anchorId,
        children: unwrapChildrenLink(n),
      }],
    }

    let lastHeading = headings[headings.length - 1];
    if (lastHeading.depth! < item.depth!) {
      // A new level.
      lastHeading.children!.push({
        type: "list",
        ordered: false,
        children: [],
      })
      lastHeading.children![lastHeading.children!.length - 1].children!.push(item);

    } else if (lastHeading.depth! >= item.depth!) {
      while (lastHeading.depth! >= item.depth!) {
        headings.pop();
        lastHeading = headings[headings.length - 1];
      }
      lastHeading.children![lastHeading.children!.length - 1].children!.push(item);
    }

    headings.push(item);
  }

  if (node.children!.length === 0) {
    return node;
  }

  postOrderVisit(node, fixDefinitionLink);
  postOrderVisit(node, restoreEmptyLink);

  node.children!.forEach((c) => collectHeading(c))
  if (headings[0].children!.length > 0) {
    node.children!.splice(1, 0, headings[0]!.children![0])
  }

  return node;
}
