import React, { useLayoutEffect, useState, useEffect } from 'react';
import "./ContentPage.scss";
import { fileService } from '../services/FileService';
import ReactMarkdown from 'react-markdown'
import Prism from "prismjs";
import { from } from 'rxjs';
import { useLocation } from 'react-router-dom';
import { CodeBlock } from './CodeBlock';
import { TocPlugin } from './TocAstPlugin';
import { Heading } from './Heading';
import { TextBlock } from './TextBlock';
import { ListItem } from './ListItem';

export const ContentPage = () => {
  const location = useLocation();
  const [content, setContent] = useState("loading");
  let fullpath = location.pathname;

  let isValid = false;
  let content$: Promise<string> | null = null;
  let contentPath = "";
  if (fullpath.startsWith('/note/')) {
    isValid = true;
    contentPath = fullpath.substring(5);
  }
  if (isValid) {
    content$ = fileService.getNoteContent(contentPath);
  }
  useLayoutEffect(() => {
    Prism.highlightAll();
  })

  useEffect(() => {
    if (!isValid) {
      return;
    }
    const sub = from(content$!).subscribe((value) => {
      setContent(value);
      if (!location.hash) {
        window.scrollTo(0, 0);
      } else {
        const hash = location.hash.replace("#", "");
        const ele = document.getElementById(hash);
        if (ele && ele.parentElement && ele.parentElement.scrollTop >= 0) {
          ele.scrollIntoView();
        }
      }
    });
    return () => sub.unsubscribe();
  }, [content$, isValid, location.hash])

  return (
    <ReactMarkdown className="content-page" source={content}
      escapeHtml={false}
      astPlugins={[TocPlugin]}
      renderers={{ code: CodeBlock, heading: Heading, text: TextBlock, listItem: ListItem }} />
  )
}
